    <!-- 讀取效果模板:vue-loading-overlay -->
    <loading :active.sync="isLoading"></loading>

    <!-- 訂單列表 -->
    <table class="table mt-4">
      <thead>
        <tr>
          <th>購買時間</th>
          <th>訂單號碼</th>
          <th>Email</th>
          <th>購買內容</th>
          <th class="text-right">應付金額</th>
          <th>是否付款</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in orders" :key="item.id">
          <td>{{ item.create_at | localDate }}</td>
          <td>{{ item.id }}</td>
          <td>{{ item.user.email }}</td>
          <td>
            <ul class="list-unstyled">
              <li
                v-for="(product, i) in item.products"
                :key="i"
              >{{ product.product.title }} 數量：{{ product.qty }} {{ product.product.unit }}</li>
            </ul>
          </td>
          <td class="text-right">{{ item.total | currency }}</td>
          <td>
            <strong class="text-success" v-if="item.is_paid">已付款</strong>
            <span class="text-muted" v-else>尚未付款</span>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- 頁碼元件 -->
    <Pagination :prop-pagination="pagination" @page="getOrders"></Pagination>

    <!-- 示範模版 -->
