<template>
  <div>
    <!-- 讀取效果模板:vue-loading-overlay -->
    <loading :active.sync="isLoading"></loading>

    <!-- 查詢訂單 -->
    <div v-if="!this.$route.params.orderId">
      <div class="input-group mt-4 input-group-sm">
        <input type="text" class="form-control" v-model="orderId" placeholder="請輸入訂單號碼" />
        <div class="input-group-append">
          <button class="btn btn-outline-secondary" type="button" @click="getOrder">查詢訂單</button>
        </div>
      </div>
      <span id="errormsg" class="text-danger"></span>
    </div>

    <!-- 表單模版 -->
    <div class="my-5 row justify-content-center" v-if="!(order === null) && order.id">
      <form class="col-md-6" @submit.prevent="payOrder">
        <table class="table">
          <thead>
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
          </thead>
          <tbody>
            <tr v-for="item in order.products" :key="item.id">
              <td class="align-middle">{{ item.product.title }}</td>
              <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
              <td class="align-middle text-right">{{ item.final_total }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="2" class="text-right">總計</td>
              <td class="text-right">{{ order.total }}</td>
            </tr>
          </tfoot>
        </table>

        <table class="table">
          <tbody>
            <tr>
              <th width="100">訂單編號</th>
              <td>{{ order.id }}</td>
            </tr>
            <tr>
              <th>Email</th>
              <td>{{ order.user.email }}</td>
            </tr>
            <tr>
              <th>姓名</th>
              <td>{{ order.user.name }}</td>
            </tr>
            <tr>
              <th>收件人電話</th>
              <td>{{ order.user.tel }}</td>
            </tr>
            <tr>
              <th>收件人地址</th>
              <td>{{ order.user.address }}</td>
            </tr>
            <tr>
              <th>付款狀態</th>
              <td>
                <span v-if="!order.is_paid">尚未付款</span>
                <span v-else class="text-success">付款完成</span>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="text-right" v-if="order.is_paid === false">
          <button class="btn btn-danger">確認付款去</button>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      orderId: "",
      order: {
        user: {}
      },
      isLoading: false
    };
  },
  methods: {
    getOrder() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${vm.orderId}`;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        console.log(response.data);
        if (response.data.order) {
          vm.order = response.data.order;
        } else {
          document.querySelector("#errormsg").textContent = "訂單號碼有誤";
        }
        vm.isLoading = false;
      });
    },
    payOrder() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${vm.orderId}`;
      vm.isLoading = true;
      this.$http.post(api).then(response => {
        console.log(response.data);
        if (response.data.success) {
          console.log("付款完成");
          vm.getOrder();
        } else {
          console.log("付款失敗");
        }
        vm.isLoading = false;
      });
    }
  },
  created() {
    this.orderId = this.$route.params.orderId;
    // console.log(this.orderId);
    if (this.orderId) {
      this.getOrder();
    }
  }
};
</script>


